<script setup lang="ts">
import type { Menu } from '@/types/global'
import useMenuStore from '@/store/modules/menu'
import useSettingsStore from '@/store/modules/settings'

defineOptions({
  name: 'SubMainSidebar',
})

const router = useRouter()

const settingsStore = useSettingsStore()
const menuStore = useMenuStore()

const { generateI18nTitle } = useMenu()

function iconName(isActive: boolean, icon?: string, activeIcon?: string) {
  let name
  if ((!isActive && icon) || (isActive && !activeIcon)) {
    name = icon
  }
  else if (isActive && activeIcon) {
    name = activeIcon
  }
  return name
}

function isHasChildren(item: Menu.recordRaw) {
  return item.children?.some(item => item.meta?.menu !== false)
}
</script>

<template>
  <div v-if="settingsStore.settings.menu.mode === 'customized'" class="sub-main-sidebar-container" data-fixed-calc-width-customized>
    <div class="menu">
      <template v-for="(item, index) in menuStore.sidebarMenus" :key="index">
        <div
          class="menu-item relative mx-1 py-2 transition-all" :class="{
            active: index === menuStore.subActived,
          }"
        >
          <div
            class="group menu-item-container h-full w-full flex cursor-pointer items-center justify-between gap-1 px-3 text-[var(--g-sub-sidebar-menu-color)] transition-all hover-bg-[var(--g-sub-sidebar-menu-hover-bg)] hover-text-[var(--g-sub-sidebar-menu-hover-color)]" :class="{
              'text-[var(--g-sub-sidebar-menu-active-color)]! bg-[var(--g-sub-sidebar-menu-active-bg)]!': index === menuStore.subActived,
              'rounded-lg': settingsStore.settings.menu.isRounded,
            }" :title="generateI18nTitle(item.meta?.title)" @click="isHasChildren(item) ? menuStore.setSubActived(index) : (item.path && router.push(item.path))"
          >
            <div class="inline-flex flex-1 items-center justify-center gap-1">
              <SvgIcon v-if="iconName(index === menuStore.subActived, item.meta?.icon, item.meta?.activeIcon)" :name="iconName(index === menuStore.subActived, item.meta?.icon, item.meta?.activeIcon)!" class="menu-item-container-icon transition-transform group-hover-scale-120" />
              <span class="w-full flex-1 truncate text-sm transition-height transition-opacity transition-width">
                {{ generateI18nTitle(item.meta?.title) }}
              </span>
            </div>
          </div>
        </div>
      </template>
    </div>
  </div>
</template>

<style scoped>
.sub-main-sidebar-container {
  position: absolute;
  z-index: 1100;
  height: var(--g-sub-main-sidebar-height);
  background: var(--g-sub-sidebar-bg);
  box-shadow: 0 1px 0 0 var(--g-border-color);

  .menu {
    display: inline-flex;
    height: 100%;
    padding: 0 4px;

    :deep(.menu-item) {
      .menu-item-container {
        color: var(--g-sub-sidebar-menu-color);

        &:hover {
          color: var(--g-sub-sidebar-menu-hover-color);
          background-color: var(--g-sub-sidebar-menu-hover-bg);
        }

        .menu-item-container-icon {
          font-size: 20px !important;
        }
      }

      &.active .menu-item-container {
        color: var(--g-sub-sidebar-menu-active-color) !important;
        background-color: var(--g-sub-sidebar-menu-active-bg) !important;
      }
    }
  }
}
</style>
